// fonts
@font-face {
  font-family: "gt-walsheim-bold";
  src: url(fonts/GT-Walsheim-Pro-Bold.woff);
  font-style: normal;
  font-weight: bolder;
}

@font-face {
  font-family: "gt-walsheim-bold-oblique";
  src: url(fonts/GT-Walsheim-Pro-Bold-Oblique.woff);
  font-style: oblique;
  font-weight: bold;
}

@font-face {
  font-family: "gt-walsheim-regular";
  src: url(fonts/GT-Walsheim-Pro-Regular.woff);
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: "gt-walsheim-regular-oblique";
  src: url(fonts/GT-Walsheim-Pro-Regular-Oblique.woff);
  font-style: oblique;
  font-weight: normal;
}

@font-face {
  font-family: "gt-walsheim-light";
  src: url(fonts/GT-Walsheim-Pro-Light.woff);
  font-style: normal;
  font-weight: lighter;
}

@font-face {
  font-family: "gt-walsheim-light-oblique";
  src: url(fonts/GT-Walsheim-Pro-Light-Oblique.woff);
  font-style: oblique;
  font-weight: lighter;
}
