//forms
@use './parcel/base/variables' as vars;
// @use '/./parcel/base/typography' as fonts;

.form-container {
  width: 600px;
  height: auto;
  margin: 50px auto;
  padding: 50px;
  background: #ffffff;
  box-shadow: -12px -12px 20px rgba(245, 245, 245, 0.5), 12px 12px 20px rgba(162, 180, 198, 0.3);
  // box-shadow: inset 2px 2px 4px rgba(245, 245, 245, 0.5);
}

input[type="text"] {
  appearance: none;
  -webkit-appearance: none;
}

.form-container h1 {
  font-family: "gt-walsheim-bold";
  font-size: 2rem;
  margin-bottom: 10px;
}

.form-container p {
  font-family: "gt-walsheim-regular";
  font-size: 1rem;
  margin-bottom: 30px;
}

input[type="text"] {
  padding: 16px;
  background-color: vars.$off-white;
  color: vars.$black;
  font-family: vars.$font-regular;
  border: 2px solid vars.$off-white;
  display: block;
  margin: 20px 0;
  width: 100%;
  outline: none;
  transition: border 0.3s;
}

input[type="text"]:focus {
  border: 2px solid vars.$black;
  border-radius: 0;
  transition: border 0.3s;
}

input[type="email"] {
  padding: 16px;
  background-color: vars.$off-white;
  color: vars.$black;
  font-family: vars.$font-regular;
  border: 2px solid vars.$off-white;
  display: block;
  margin: 20px 0;
  width: 100%;
  outline: none;
  transition: border 0.3s;
}

input[type="email"]:focus {
  border: 2px solid vars.$black;
  border-radius: 0;
  transition: border 0.3s;
}

