select {
  -webkit-appearance: none;
  appearance: none;
  padding: 16px;
  background-color: vars.$white;
  color: vars.$black;
  font-family: vars.$font-bold;
  border: 2px solid vars.$black;
  border-radius: 0;
  display: block;
  margin: 16px 0;
  width: 100%;
  outline: none;
  text-transform: uppercase;
}

select::after {
  content: "<";
  color: black;
  font: 17px "Consolas", monospace;
  transform: rotate(90deg);
}

input[type="checkbox"] {
  appearance: none;
  background-color: vars.$white;
  color: vars.$off-white;
  font-family: vars.$font-bold;
  border: 2px solid vars.$black;
  padding: 10px;
  display: inline-block;
  outline: none;
  transition: 0.1s;
  margin: 15px 15px 2px 0;
}

input[type="checkbox"]:checked {
  background-color: vars.$blue;
  border: 2px solid vars.$blue;
  transition: 0.1s;
}

#checkbox-label {
  color: vars.$black;
  font-family: vars.$font-bold;
  font-size: 0.8rem;
  white-space: pre-line;
  line-height: 8px;
  padding-top: 0;
  display: inline-block;
}

button[type="submit"] {
  outline: none;
  padding: 16px 50px;
  background-color: vars.$purple;
  color: vars.$white;
  font-size: 1rem;
  max-width: 300px;
  font-family: vars.$font-bold;
  border: 2px solid vars.$off-white;
  display: block;
  margin: 16px 0;
  text-align: center;
  transition: background-color 0.3s;
  border: 2px solid vars.$purple;
  margin-top: 20px;
}

button[type="submit"]:hover {
  transition: 0.3s;
  color: vars.$black;
  background-color: vars.$white;
  border: 2px solid vars.$purple;
}

.dxc-logo-form {
  width: 30%;
}
